import { newArticle } from '../../apis/article'
export default {
  name: 'edit',
  data() {
    return {
      article: {
        body: '',
        description: '',
        tagList: [],
        title: '',
        tag: ''
      }
    }
  },
  methods: {
    async createNewArticle() {
      const params = Object.assign(this.article)
      delete params.tag
      try {
        await newArticle(params)
        this.$router.push('/')
      } catch (error) {
        console.error(error)
      }
    },
    AddTagToTagList() {
      this.article.tagList.push(this.article.tag)
      this.article.tag = ''
    }
  }
}
